body {
  margin: 10px auto;
  max-width: 400px;
}
.heading {
  text-align: center;
}
.items-list {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
}

.finished {
  text-decoration: line-through;
}
.card {
  background-color: #fff;
  border-radius: 3px;
  padding: 20px;
  box-shadow: 0 0 0 1px rgb(16 22 26 / 10%), 0 1px 1px rgb(16 22 26 / 20%),
    0 2px 6px rgb(16 22 26 / 20%);
}

.input-wrapper {
  display: flex;
}
.input {
  flex-grow: 1;
  border-right: none;
  background: #fff;
  border: none;
  border-radius: 3px 0 0 3px;
  box-shadow: 0 0 0 0 rgb(19 124 189 / 0%), 0 0 0 0 rgb(19 124 189 / 0%),
    inset 0 0 0 1px rgb(16 22 26 / 15%), inset 0 1px 1px rgb(16 22 26 / 20%);
  color: #182026;
  font-size: 14px;
  font-weight: 400;
  height: 30px;
  line-height: 30px;
  outline: none;
  padding: 0 10px;
}
.input:focus {
  box-shadow: 0 0 0 1px #137cbd, 0 0 0 3px rgb(19 124 189 / 30%),
    inset 0 1px 1px rgb(16 22 26 / 20%);
}
.add-btn {
  width: 100px;
  background-color: #137cbd;
  background-image: linear-gradient(
    180deg,
    hsla(0, 0%, 100%, 0.1),
    hsla(0, 0%, 100%, 0)
  );
  box-shadow: inset 0 0 0 1px rgb(16 22 26 / 40%), inset 0 -1px 0 rgb(16 22 26 /
          20%);
  color: #fff;
  border-radius: 0 3px 3px 0;
  cursor: pointer;
  border: none;
  padding: 5px 10px;
}
.add-btn:focus {
  outline: 2px auto rgba(19, 124, 189, 0.6);
  outline-offset: 2px;
}
.tag {
  background-color: rgba(138, 155, 168, 0.2);
  color: #182026;
  margin: 8px 0;
  min-height: 30px;
  min-width: 30px;
  padding: 5px 10px;
  border-radius: 3px;
  display: flex;
  align-items: center;
  display: flex;
  cursor: pointer;
}
.label {
  margin-left: 4px;
  flex-grow: 1;
}
.remove-btn {
  border: none;
  background-color: transparent;
  font-size: 24px;
  cursor: pointer;
}
.remove-btn span {
  display: inline-block;
  transform: rotateY(0deg) rotate(45deg);
  opacity: 0.5;
}
body {
  background: #fff;
  color: #333;
  font-family: Lato, sans-serif;
  background-color: #e4e4e4;
}

.container {
  display: block;
  width: 400px;
  margin: 10px auto 100px;
  background-color: #fff;
  padding: 0px 10px 10px 10px;
  border-radius: 10px;
}

h2 {
  text-align: center;
  padding-top: 10px;
  margin-bottom: 0px;
}

ul {
  margin: 0;
  padding: 0;
}

li * {
  float: left;
}

li,
h3 {
  clear: both;
  list-style: none;
}

input,
button {
  outline: none;
}

button {
  background: none;
  border: 0px;
  color: #888;
  font-size: 15px;
  width: 60px;
  margin: 10px 0 0;
  font-family: Lato, sans-serif;
  cursor: pointer;
}

button:hover {
  color: #333;
}

/* Heading */

h3,
label[for="new-task"] {
  color: #333;
  font-weight: 700;
  font-size: 15px;
  border-bottom: 2px solid #333;
  padding: 20px 0 10px;
  margin: 0;
  text-transform: uppercase;
}

input[type="text"] {
  margin: 0;
  font-size: 18px;
  line-height: 18px;
  height: 18px;
  padding: 10px;
  border: 1px solid #ddd;
  background: #fff;
  border-radius: 6px;
  font-family: Lato, sans-serif;
  color: #888;
}

input[type="text"]:focus {
  color: #333;
}

/* New Task */

label[for="new-task"] {
  display: block;
  margin: 0 0 20px;
}

input#new-task {
  float: left;
  width: 318px;
}

p > button:hover {
  color: #0fc57c;
}

/* Task list */

li {
  overflow: hidden;
  padding: 20px 0;
  border-bottom: 1px solid #eee;
}

li > input[type="checkbox"] {
  margin: 0 10px;
  position: relative;
  top: 15px;
}

li > label {
  font-size: 18px;
  line-height: 40px;
  width: 237px;
  padding: 0 0 0 11px;
}

li > input[type="text"] {
  width: 226px;
}

li > .delete:hover {
  color: #cf2323;
}

/* Completed */

#completed-tasks label {
  text-decoration: line-through;
  color: #888;
}

/* Edit Task */

ul li input[type="text"] {
  display: none;
}

ul li.editMode input[type="text"] {
  display: block;
}

ul li.editMode label {
  display: none;
}